class Homepage {
  constructor (data) {
    this._id = data._id
    this.domain = data.domain || ''
    this.logo = data.domain || ''
    this.icon = data.icon || ''
    this.theme = data.theme || ''
    this.profile = data.profile || {
      name: '',
      president: '',
      create: '',
      capital: '',
      license: '',
      assurance: '',
      post: '',
      address: '',
      phone: '',
      fax: '',
      email: '',
      holiday: '',
      latitude: '',
      logitude: ''
    }
    this.vision = data.profile || {
      title: '',
      description: ''
    }
    this.services = data.services || []
    this.pages = data.pages || []
  }
  static getNewPage () {
    return {
      id: '',
      icon: '',
      name: null,
      components: []
    }
  }
  static getNewComponent (id) {
    return {
      id: id
    }
  }
  toJSON () {
    return {
      _id: this._id,
      domain: this.domain,
      profile: this.profile,
      pages: this.pages,
      logo: this.logo,
      icon: this.icon,
      theme: this.theme,
      services: this.services,
      vision: this.vision
    }
  }
}

export default Homepage



