<template>
  <div>
    <md-button class="md-raised md-primary" @click="cancel()">取消</md-button>
    <md-button class="md-raised md-primary" @click="save()">保存</md-button>

    <md-tabs md-sync-route v-if="manager.homepageService.edithomepage">
      <md-tab id="tab-basic" md-label="基本情報">
        <div class="md-layout md-gutter md-alignment-left">
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="nationality">ドメイン</label>
              <md-input ref="nationality" v-model="manager.homepageService.edithomepage.domain"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="nationality">テーマ</label>
              <md-input ref="nationality" v-model="manager.homepageService.edithomepage.theme"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">ロゴ</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.logo"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">アイコン</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.icon"></md-input>
            </md-field>
          </div>
        </div>
      </md-tab>
      <md-tab id="tab-comnapy" md-label="会社情報">
        <div class="md-layout md-gutter md-alignment-left">
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="nationality">会社名</label>
              <md-input ref="nationality" v-model="manager.homepageService.edithomepage.profile.name"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">代表取締役</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.ceo"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">設立年月日</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.createdate"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">資本金</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.capital"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">免許番号</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.license"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">郵便番号</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.post"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">所在地</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.address"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">電話</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.phone"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">ファクス</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.fax"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">メール</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.mail"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-50 md-xsmall-size-100">
            <md-field>
              <label for="profession">定休日</label>
              <md-input ref="profession" v-model="manager.homepageService.edithomepage.profile.holiday"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-size-100">
            <md-field>
              <label for="profession">軽度</label>
              <md-textarea ref="profession" v-model="manager.homepageService.edithomepage.profile.logitude"></md-textarea>
            </md-field>
          </div>
          <div class="md-layout-item md-size-100">
            <md-field>
              <label for="profession">緯度</label>
              <md-textarea ref="profession" v-model="manager.homepageService.edithomepage.profile.latitude"></md-textarea>
            </md-field>
          </div>
        </div>
      </md-tab>

      <md-tab id="tab-page" md-label="ページ設定">
        <div class="md-layout md-gutter md-alignment-left">

          <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-if="pages.length>0" v-for="(page, pageindex) in pages" :key="pageindex">
            <md-card md-with-hover class="page-card">
              <md-ripple>
                <md-card-content class="page-content" v-if="page.id!=''">
                  <div class="md-layout md-alignment-center-left">
                    <md-field>
                      <md-input :class="{'md-invalid': !isNameValid}" v-model="page.name"></md-input>
                    </md-field>
                    <div class="md-title" v-if="page.id==''">ページ新規作成</div>
                  </div>
                  <div class="md-layout md-alignment-center-left">
                    <draggable v-model="page.components" element="md-list" :options="{group:'people'}" @start="drag=true" @end="drag=false">
                      <md-list-item class="md-layout-item md-size-100" v-for="(component, componentindex) in page.components" :key="componentindex">
                        <md-chip class="md-raised md-primary component-icon" md-deletable
                          @md-delete="deleteComponent(pageindex, page.components, componentindex)"
                          @click="editComponent(component)">
                          {{manager.const.component[component.id]}}
                        </md-chip>
                      </md-list-item>
                      <!-- <button slot="footer" @click="addComponent(pageindex, page.component)"><md-icon>add</md-icon></button> -->
                      <md-menu class="md-layout-item md-size-100 addcomponent-wrapper" slot="footer" md-direction="bottom-end">
                        <md-button class="md-raised addcomponent" md-menu-trigger><md-icon>add</md-icon></md-button>
                        <md-menu-content>
                          <md-menu-item
                            v-for="(componentname, key) in manager.const.component"
                            :key="key"
                            @click="addComponent(pageindex, page.components, key)"
                            >{{componentname}}</md-menu-item>
                        </md-menu-content>
                      </md-menu>
                    </draggable>
                  </div>
                </md-card-content>

                <md-card-content class="page-content-empty" v-else>
                  <div class="md-layout md-gutter md-alignment-left">
                    <div class="md-layout-item md-size-100">
                      <md-field :class="{'md-invalid': !isNameValid}">
                        <label>タイトル</label>
                        <md-input v-model="page.name" required></md-input>
                        <span class="md-error" v-if="!page.name">入力してください</span>
                      </md-field>
                    </div>
                    <div class="md-layout-item md-size-100">
                      <md-field>
                        <label>アイコン</label>
                        <md-input v-model="page.icon"></md-input>
                      </md-field>
                    </div>
                    <div class="md-layout-item md-size-100 addpage">
                      <md-button class="md-primary md-icon-button md-raised" @click.stop="addPage"><md-icon>add</md-icon></md-button>
                    </div>
                  </div>
                </md-card-content>

                <md-card-actions class="page-action">
                  <md-button class="md-primary md-icon-button md-raised" v-if="page.id!=''" @click.stop="deletePage(pageindex)"><md-icon>delete_forever</md-icon></md-button>
                </md-card-actions>
              </md-ripple>
            </md-card>
          </div>
        </div>
      </md-tab>
    </md-tabs>
  </div>
</template>

<script>
import Homepage from '@/store/models/Homepage.js'
import draggable from 'vuedraggable'
import utils from '@/tool/utils.js'
export default {
  props: ['manager'],
  components: {
    Homepage,
    draggable
  },
  data: () => ({
    inValidObj: '',
    isCreateNew: true,
    pages: [],
    pages_bak: []
  }),
  computed: {
    isNameValid () {
      let newpage = this.pages[this.pages.length - 1]
      if (newpage.name === '') return false
      return true
    }
  },
  created () {
    let that = this
    if (that.manager.homepageService.edithomepage._id) {
      this.isCreateNew = false
    }

    if (that.manager.homepageService.edithomepage) {
      that.manager.homepageService.edithomepage.pages.forEach(one => {
        that.pages.push(one)
      })
      that.pages.push(Homepage.getNewPage())
    }
  },
  methods: {
    cancel () {
      this.$router.push({path: '/my/homepage'})
    },
    save () {
      let that = this

      that.manager.homepageService.edithomepage.pages.splice(0, that.manager.homepageService.edithomepage.pages.length)
      that.pages.forEach(onePage => {
        if (onePage.id !== '') {
          that.manager.homepageService.edithomepage.pages.push(onePage)
        }
      })
      if (that.isCreateNew) {
        that.manager.homepageService.insertHomepageSetting(that.manager.homepageService.edithomepage, () => {
          that.$router.push({path: '/my/homepage'})
        })
      } else {
        that.manager.homepageService.updateHomepageSetting(that.manager.homepageService.edithomepage, () => {
          that.$router.push({path: '/my/homepage'})
        })
      }
    },
    editComponent (component) {
      if (component.id === 'htmltext') {
        utils.event.$emit('SHOW_DETAIL', 'customizePageComponentEdit', component, true)
      }
    },
    addPage () {
      let newpage = this.pages[this.pages.length - 1]

      if (newpage.name == null) {
        newpage.name = ''
      }

      if (!this.isNameValid) {
        return '入力不正です。'
      }

      newpage.id = new Date().getTime()
      this.pages.push(Homepage.getNewPage())
    },
    addComponent (pageindex, component, componentid) {
      let that = this
      let newcomponent = Homepage.getNewComponent(componentid)
      component.push(newcomponent)
      that.$nextTick(() => {
        that.clone(pageindex)
        that.clean(pageindex)
        that.$nextTick(() => {
          that.reset(pageindex)
        })
      })
    },
    deletePage (index) {
      this.pages.splice(index, 1)
    },
    deleteComponent (pageindex, component, componentindex) {
      let that = this
      component.splice(componentindex, 1)
      console.log('deleteComponent: pageindex(' + pageindex + ')')
      that.$nextTick(() => {
        that.clone(pageindex)
        that.clean(pageindex)
        that.$nextTick(() => {
          that.reset(pageindex)
        })
      })
    },
    clean (index) {
      if (index != null) {
        console.log('clean page' + index)
        this.pages.splice(index, 1)
      } else {
        this.pages.splice(0, this.pages.length)
      }
    },
    clone (index) {
      let that = this
      if (index != null) {
        console.log('clone page' + index)
        that.pages_bak.push(JSON.stringify(that.pages[index]))
      } else {
        that.pages.forEach(one => {
          console.log('clone page')
          that.pages_bak.push(JSON.stringify(one))
        })
      }
    },
    reset (index) {
      let that = this
      if (index != null) {
        console.log('reset page' + index)
        that.pages.splice(index, 0, JSON.parse(that.pages_bak))
        that.pages_bak.splice(0, 1)
      } else {
        console.log('reset')
        that.pages_bak.forEach(one => {
          console.log('reset page')
          that.pages.push(JSON.parse(one))
        })
        that.pages_bak.splice(0, that.pages_bak.length)
      }
    }
  }
}
</script>
<style scoped lang="scss">
.page-header {
  min-height: 70px;
}
.page-card {
  margin-top: 10px;
}
.page-content {
  min-height: 300px;
}
.page-content-empty {
  min-height: 300px;
}
.addpage {
  display: flex;
  justify-content: center;
  align-items: center;
}
.addcomponent-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-action {
  min-height: 60px;
}
.component-icon {
  text-align: center;
  width: 100%;
  height: 40px;
  padding-top: 5px;
}
.md-list {
  width: 100%;
}

</style>



