<template>
<div>
  <div class="md-layout md-gutter md-alignment-top-right">
    <div class="md-layout-item md-size-100 resource-button-wrapper">
      <md-button class="md-raised md-accent md-icon-button resource-button" @click="cancel()" v-if="editmode=='addnew'"><md-icon>delete_forever</md-icon></md-button>
      <md-button class="md-raised md-accent md-icon-button resource-button" @click="save()" v-if="editmode=='addnew'"><md-icon>save</md-icon></md-button>
      <md-button class="md-raised md-accent md-icon-button resource-button" @click="editmode='addnew'" v-if="editmode=='view'"><md-icon>add</md-icon></md-button>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-top-left" v-if="editmode=='addnew'">
    <div class="md-layout-item md-size-100">
      <uploadFiles ref="resourceUploader" :manager="manager"></uploadFiles>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-top-left">
    <div class="md-layout-item userResource md-xsmall-size-50 md-small-size-33  md-medium-size-25  md-large-size-20"
      v-for="(userResource, index) in manager.userResourceService.userResources"
      :key="index">
      <md-card md-with-hover>
        <md-card-content >
          <img :src="userResource.thumbnail">
          <div class="userResource-wrapper">
            {{userResource.name}}
          </div>
        </md-card-content>
        <md-button class="md-accent md-icon-button resource-delete-button" @click.stop="deleteResource(userResource)"><md-icon>highlight_off</md-icon></md-button>
      </md-card>
    </div>
  </div>
</div>
</template>

<script>
import uploadFiles from '@/components/common/uploadFiles'
export default {
  props: ['manager'],
  components: {
    uploadFiles
  },
  data: () => ({
    editmode: 'view'
  }),
  created () {
    this.manager.userResourceService.preloadUserResources(null, null, null)
  },
  methods: {
    cancel () {
      let that = this
      that.$refs.resourceUploader.clear()
      that.editmode = 'view'
    },
    save () {
      let that = this
      that.$refs.resourceUploader.upload((photos) => {
        console.log(photos)
        if (photos && photos.length > 0) {
          photos.forEach(file => {
            this.manager.userResourceService.deleteUserResources(photos, () => {
              that.editmode = 'view'
            })
          })
        }
      })
    },
    deleteResource (resource) {
      let that = this
      this.manager.userResourceService.deleteUserResources(resource, () => {
        that.editmode = 'view'
      })
    }
  }

}
</script>

<style scoped lang="scss">
.userResource-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  word-break: break-all;
  height: 5px;
}
.resource-button-wrapper {
  -webkit-box-pack: end;
  justify-content: flex-end;
  -webkit-box-align: start;
  align-items: flex-start;
}
.resource-button {
  float: right;
}
.resource-delete-button {
  position: absolute;
  top: 0px;
  right: 0px;
}
.md-layout {
  margin-top: 5px;
}
.md-card {
  margin-top: 10px;
}
</style>



