<template>
<div>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-50 md-small-size-100">
      <md-field>
        <gmap-autocomplete ref="searchPlace" class="md-input search-place-input" @place_changed="setPlace" :selectFirstOnEnter="true" placeholder="場所で緯度と経度を特定">
        </gmap-autocomplete>
        <!-- <md-button class="md-icon-button search-place" @click="usePlace"><md-icon>search</md-icon></md-button> -->
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50">
      <md-field :class="{'md-invalid': !isLatitudeValid}">
        <label for="name">緯度</label>
        <md-input v-model="latitude" type="number"></md-input>
        <span class="md-error">-90〜90の数値を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50">
      <md-field :class="{'md-invalid': !isLongitudeValid}">
        <label for="name">経度</label>
        <md-input v-model="longitude" type="number"></md-input>
        <span class="md-error">-180〜180の数値を入力してください</span>
      </md-field>
    </div>
  </div>
  <gmap-map class="gmap" :center="center" :zoom="14">
    <gmap-marker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.position"
      :clickable="false"
      :draggable="true"
      @dragend="drag($event)"
      :title="manager.mySellService.sellPiece.address"
    ></gmap-marker>
  </gmap-map>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager'],
  data: () => ({
    init: {lat: 35.7090259, lng: 139.73199249999993},
    latitude: '',
    longitude: '',
    center: {lat: null, lng: null},
    markers: [],
    place: null
  }),
  created () {
    this.markers.splice(0, this.markers.length)
    if (manager.mySellService.sellPiece.latitude != null && manager.mySellService.sellPiece.longitude != null) {
      this.center.lat = manager.mySellService.sellPiece.latitude
      this.center.lng = manager.mySellService.sellPiece.longitude
      this.markers.push({
        position: {
          lat: manager.mySellService.sellPiece.latitude,
          lng: manager.mySellService.sellPiece.longitude
        }
      })
      this.latitude = '' + manager.mySellService.sellPiece.latitude
      this.longitude = '' + manager.mySellService.sellPiece.longitude
    } else {
      this.center.lat = this.init.lat
      this.center.lng = this.init.lng
      this.markers.push({
        position: {
          lat: this.init.lat,
          lng: this.init.lng
        }
      })
      this.latitude = '' + this.init.lat
      this.longitude = '' + this.init.lng
    }
  },
  watch: {
    latitude () {
      this.markers[0].position.lat = parseFloat(this.latitude)
      this.center.lat = parseFloat(this.latitude)
    },
    longitude () {
      this.markers[0].position.lng = parseFloat(this.longitude)
      this.center.lng = parseFloat(this.longitude)
    }
  },
  computed: {
    isLatitudeValid () {
      if (parseFloat(this.latitude) < -90 || parseFloat(this.latitude) > 90) return false
      return true
    },
    isLongitudeValid () {
      if (parseFloat(this.longitude) < -180 || parseFloat(this.longitude) > 180) return false
      return true
    }
  },
  methods: {
    setPlace (place) {
      this.place = place
      this.usePlace()
    },
    usePlace () {
      if (this.place && this.place.geometry && this.place.geometry.location) {
        this.latitude = this.place.geometry.location.lat()
        this.longitude = this.place.geometry.location.lng()
        this.place = null
        this.$refs.searchPlace.$el.value = ''
      }
    },
    drag (event) {
      this.latitude = '' + event.latLng.lat()
      this.longitude = '' + event.latLng.lng()
    },
    validate () {
      if (!this.isLatitudeValid || !this.isLongitudeValid) {
        return '入力不正です。'
      }
      if (parseFloat(this.latitude) !== this.init.lat) manager.mySellService.sellPiece.latitude = parseFloat(this.latitude)
      if (parseFloat(this.longitude) !== this.init.lng) manager.mySellService.sellPiece.longitude = parseFloat(this.longitude)
      return null
    }
  }
}
</script>

<style scoped lang="scss">
// .search-place {
//   position: absolute;
//   top: 10px;
//   right: 0px;
// }
.search-place-input {
  width: 100%;
}
.gmap {
  width: 100%;
  height: 400px;
}

$md-input-height: 32px;
$md-transition-stand-timing: cubic-bezier(.25, .8, .25, 1);
$md-transition-stand-duration: .4s;
$md-transition-stand: $md-transition-stand-duration $md-transition-stand-timing;

.search-place-input {
  font-size: 16px !important;
  height: $md-input-height;
  padding: 0;
  display: block;
  flex: 1;
  border: none;
  background: none;
  transition: $md-transition-stand;
  transition-property: font-size, padding-top, color;
  font-family: inherit;
  font-size: 1px;
  line-height: $md-input-height;

  &[disabled] {
    cursor: default;
  }

  &:focus {
    outline: none;
  }

  &::-webkit-input-placeholder {
    font-size: 16px;
    text-shadow: none;
    -webkit-text-fill-color: initial;
    transition: $md-transition-stand;
    transition-property: font-size, color;
  }
}
</style>



