import utils from '@/tool/utils.js'
import Homepage from '@/store/models/Homepage.js'

class HomePageService {
  constructor () {
    this.homepages = []
    this.edithomepage = null
  }

  init () {
    this.homepages.splice(0, this.homepages.length)
    this.edithomepage = null
  }

  loadHomePages = (to, from, next) => {
    console.log('loadHomePages start')
    this.init()
    this.findHomePage({}, 0).then(
      () => {
        next && next()
      }
    )
  }

  loadHomePageSetting = (to, from, next) => {
    let that = this
    console.log('loadHomePageSetting start')
    this.init()
    if (to.params.id === 'new') {
      that.edithomepage = new Homepage({})
      next()
    } else {
      utils.restGet('/private/getHomepage', {_id: to.params.id}).then(
        response => {
          if (response) {
            that.edithomepage = new Homepage(response)
            next()
          }
        }
      )
    }
  }

  async findHomePage (params, page) {
    let that = this
    await utils.restGet('/private/getMyHomepages', {filter: params}).then(response => {
      let datas = response
      that.homepages.splice(0, that.homepages.length)
      if (datas) {
        datas.forEach(one => {
          that.homepages.push(new Homepage(one))
        })
      }
    })
  }

  insertHomepageSetting (setting, next) {
    let that = this
    console.log('insertHomepageSetting')
    utils.restPut('/private/insertHomepage', setting).then(savedSetting => {
      if (savedSetting) {
        that.loadHomePages(null, null, next)
      }
    })
  }

  updateHomepageSetting (setting, next) {
    console.log('updateHomepageSetting start')
    utils.restPost('/private/updateHomepage', setting).then(savedSetting => {
      if (savedSetting) {
        console.log('updateHomepageSetting end')
        next()
      }
    })
  }

  deleteHomepageSetting (setting, next) {
    let that = this
    utils.restPost('/private/deleteHomepage', {_id: setting._id}).then(savedSetting => {
      if (savedSetting) {
        for (let i in that.homepages) {
          if (that.homepages[i]._id === setting._id) {
            that.homepages.splice(i, 1)
          }
        }
        utils.event.$emit('SHOW_MESSAGE', {code: 'I007'}, () => {
          // that.loadHomePages(null, null, next)
        })
      }
    })
  }
}

export default new HomePageService()



