<template>
<div>
  <vue-headful title="ブドウさん"/>
  <div class="subtitle-wrapper">
    <span class="subTitle">HomePage</span>
  </div>
  <div>
    <md-tabs md-sync-route>
      <md-tab id="tab-domain" md-label="ドメイン管理">
        <div class="md-layout md-gutter md-alignment-top-right">
          <div class="md-layout-item md-size-100 resource-button-wrapper">
            <md-button class="md-icon-button md-raised md-accent button-createnew" to="/my/homepage/new">
              <md-icon>add</md-icon>
            </md-button>
          </div>
        </div>
        <div class="md-layout md-gutter md-alignment-top-left">
          <div class="md-layout-item md-size-100">
            <md-table md-card>
              <md-table-row>
                <!-- <md-table-head md-numeric>ID</md-table-head> -->
                <md-table-head>ドメイン</md-table-head>
                <md-table-head>削除</md-table-head>
              </md-table-row>

              <md-table-row
                v-for="(homepage, index) in manager.homepageService.homepages"
                :key="index"
                @click.stop="modify(homepage._id)">
                <!-- <md-table-cell md-numeric>{{index + 1}}</md-table-cell> -->
                <md-table-cell>{{homepage.domain}}</md-table-cell>
                <md-table-cell>
                  <!-- <md-button class="md-icon-button md-accent" @click.stop="modify(homepage._id)">
                    <md-icon>mode_edit</md-icon>
                  </md-button> -->
                  <md-button class="md-icon-button md-accent" @click.stop="deleteSite(homepage._id)">
                    <md-icon>delete_forever</md-icon>
                  </md-button>
                </md-table-cell>
              </md-table-row>
            </md-table>
          </div>
        </div>
      </md-tab>
      <md-tab id="tab-resource" md-label="リソース管理">
        <ResourceManage :manager="manager"></ResourceManage>
      </md-tab>
    </md-tabs>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import ResourceManage from './resourceManage'
export default {
  props: ['manager'],
  components: {
    ResourceManage
  },
  methods: {
    modify (id) {
      this.$router.push({path: '/my/homepageSetting/' + id})
    },
    deleteSite (id) {
      let that = this
      if (id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I010'}, () => {
          that.manager.homepageService.deleteHomepageSetting({_id: id})
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.subtitle-wrapper{
  position: relative;
  padding: 15px;
}
.subTitle{
  font-size: 30px;
  font-weight: 500;
}
.button-createnew{
  float: right;
}
.md-card {
  margin-top: 10px;
}
</style>



