<template>
<div class="page-container">
  <router-view :manager="manager"/>
  <lockScreen :manager="manager"/>
  <messageDialog :manager="manager"/>
  <snackBar :manager="manager"/>
</div>
</template>

<script>
import lockScreen from '@/components/common/lockScreen'
import messageDialog from '@/components/common/messageDialog'
import snackBar from '@/components/common/snackBar'
export default {
  props: ['manager'],
  components: {
    lockScreen,
    messageDialog,
    snackBar
  }
}
</script>

<style lang="scss">
@import "~vue-material/dist/theme/engine";
@include md-register-theme("default", (
  primary: #7065a3,
  accent: #c97586
));
@include md-register-theme("warm", (
  primary: #4ea07c,
  accent: #c97586
));
@include md-register-theme("light", (
  primary: #ebe48b,
  accent: #c97586
));
@import "~vue-material/dist/theme/all";
@import "~vue-material/dist/base/theme";

.primary-gradient {
  background: linear-gradient(to right, #6b52b8 0%,#9185be 100%);
}
.title-gradient {
  background: -webkit-linear-gradient(bottom, #563e9e, #7e71ab);
  background: linear-gradient(to top right, #563e9e, #7e71ab);
  background: -ms-linear-gradient(bottom, #563e9e, #7e71ab) !important;
  background: -moz-linear-gradient(center bottom, #563e9e 0%, #7e71ab 100%) !important;
  background: -o-linear-gradient(#563e9e, #7e71ab) !important;
}
.primary-active:before, .primary-active:after {
  border-top: 4px solid #563e9e;
}

// @import url('https://fonts.googleapis.com/earlyaccess/notosansjapanese.css');
@import url('https://fonts.googleapis.com/earlyaccess/roundedmplus1c.css');

// common
html, body {
  // font-family: 'Noto Sans Japanese';
  font-family: 'Rounded Mplus 1c';
}
.bd-xsmall-hide, .bd-small-hide {
  .md-tabs-navigation {
    background: rgba(0, 0, 0, 0) !important;
  }
}
.page-container {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.md-app-content {
  padding: 0px;
  border: none;
}
.app-content-auto-height {
  width: 100%;
  height: auto;
  padding: 16px;
  overflow: hidden;
}
.app-content-fix-height {
  width: 100%;
  height: 100%;
  overflow: scroll;
}

// icon and text in one row
.icon-text {
  display: flex;
  justify-content: center;
  align-items: center;
  .md-icon {
    margin-right: 5px;
  }
}

// hover cursor
.md-with-hover {
  cursor: default !important;
}

// dialog selection
.md-select-menu {
  z-index: 10;
}

// fix tab height
.md-tabs-content {
  height: auto !important;
}

// chat
// @media (max-width: 600px) {
//   .md-app-container {
//     padding-left: 0px !important;
//   }
// }

// function class
.bd-xsmall-show {
  display: none !important;
}
@media (max-width: 600px) {
  .bd-xsmall-hide {
    display: none !important;
  }
  .bd-xsmall-show {
    display: block !important;
  }
}
.bd-small-show {
  display: none !important;
}
@media (max-width: 944px) {
  .bd-small-hide {
    display: none !important;
  }
  .bd-small-show {
    display: block !important;
  }
}
.bd-medium-show {
  display: none !important;
}
@media (max-width: 1264px) {
  .bd-medium-hide {
    display: none !important;
  }
  .bd-medium-show {
    display: block !important;
  }
}

// google map
.pac-item {
  cursor: pointer;
}
</style>



